










































































import Vue from "vue";
import { Component } from "vue-property-decorator";
import Layout from "@/components/Layout.vue";
import Icon from "@/components/Icon.vue";
import axios from "axios";
import { message } from "ant-design-vue";

@Component({ components: { Layout, Icon } })
export default class My extends Vue {
  userId = "";
  isUseMsgLogin = false;
  userPassword = "";
  isEntranceVi = true;
  inputPlaceholder = "请输入账号";
  inputPswPlaceholder = "请输入密码";
  isBtnRegister = false;
  isLogined = this.$store.state.isLogined;
  confirmPsw = "";
  isChangePsw = false;
  oldPsw = "";
  newPsw = "";
  confirmNewPsw = "";

  handleLogin() {
    console.log({
      userPassword: this.userPassword,
      userId: this.userId,
    });

    axios.interceptors.request.use(
      (config) => {
        let token = localStorage.getItem("token");
        if (token) {
          token = JSON.parse(token);
          // 判断是否存在token，如果存在的话，则每个http header都加上token
          config.headers.Authorization = token;
        }
        return config;
      },
      (error) => {
        return Promise.reject(error);
      }
    );

    if (this.isBtnRegister === true) {
      //
      this.doRegister();
    } else {
      axios
        .post("http://localhost:3000/api/user/login", {
          password: this.userPassword,
          userName: this.userId,
          useMsg: false,
          verificationCode: "",
        })
        .then((result) => {
          if (result.data.code === "0") {
            localStorage.setItem("token", JSON.stringify(result?.data?.data));
            message.success("登录成功");
            this.isLogined = true;
            this.$store.commit("changeLoginState");
            this.$store.commit("updateUserId", this.userId);
            axios.post("http://localhost:3000/getAccountBook", {});
            this.$store.dispatch("getSettings", this.userId).then(() => {});
          } else {
            message.warning(result.data.msg || "登录失败");
          }
        });
    }
  }
  doRegister() {
    if (this.userId === "") {
      message.warn("请输入账号");
    } else if (this.userPassword === "") {
      message.warn("请输入密码");
    } else if (this.confirmPsw === "") {
      message.warn("请确认密码");
    } else if (this.userPassword !== this.confirmPsw) {
      message.warn("两次输入密码不一致");
    } else {
      axios
        .post("http://localhost:3000/api/user/register", {
          userName: this.userId,
          password: this.userPassword,
          useMsg: this.isUseMsgLogin,
        })
        .then((result: any) => {
          if (result.data.code === 0) {
            message.success(result.data.msg || "注册成功");
            this.isBtnRegister = false;
            this.userPassword = "";
            this.userId = "";
            this.confirmPsw = "";
          } else {
            message.warn(result.data.msg || "注册失败");
          }
        })
        .catch((err) => {
          console.warn(err);
        });
    }
  }
  changePsw() {
    this.isChangePsw = true;
    // axios.post("http://localhost:3000/api/user/update/password",{
    //   userId:this.userId,
    // });
  }
  logout() {
    localStorage.removeItem("token");
    this.isLogined = false;
    this.$store.commit("changeLoginState");
    this.userId = "";
    this.userPassword = "";
    this.confirmPsw = "";
  }
  handleShift() {
    this.isUseMsgLogin = !this.isUseMsgLogin;
    this.isBtnRegister = false;
    if (this.isUseMsgLogin === true) {
      this.inputPlaceholder = "请输入手机号";

      this.inputPswPlaceholder = "请输入验证码";
    } else {
      this.inputPlaceholder = "请输入账号";
      this.inputPswPlaceholder = "请输入密码";
    }
  }
  handleRegister() {
    this.isBtnRegister = !this.isBtnRegister;
    this.isUseMsgLogin = false;
    // if (this.isUseMsgLogin === true) {
    //   const telNum = this.userId;
    //   axios.post("/api/", {}).then(() => {
    //     //
    //   });
    // } else {
    //   //
    // }
  }
  handleSetting() {
    this.isEntranceVi = false;
  }
  mounted() {
    window.addEventListener(
      "hashchange",
      () => {
        const currentPath = window.location.hash.slice(1);
        console.log("-----");
        if (currentPath === "/my") {
          this.isEntranceVi = true;
        } else {
          this.isEntranceVi = false;
        }
      },
      false
    );

    axios.post("http://localhost:3000/api/account/fetch/settings", {
      useId: "001",
    });
  }
}
